.miq-user-avatar {
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    overflow: hidden;
    background-color: var(--blue-50);
    border: 2px solid white;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &-group {
        display: flex;
        direction: rtl;

        .miq-user-avatar {
            margin: -6px;

            &.count {
                margin-left: 8px;
            }
        }
    }
}
